.login {
    margin-top: 100px;
    width: 100%;
    margin-bottom: 70px;
  }
  .login-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90% !important;
  }
  .login-img-cont {
    width: 100px;
    height: 100px;
   margin-left: 25px;
  }
  .login-logo {
    width: 100px;
    height: 100px;
    text-align: center;
  }
  .login-text {
    text-align: center;
    color: rgb(177, 177, 177);
    text-transform: uppercase;
    font-size: 0.9rem;
    margin-top: 30px;
  }
  
  .form {
    width: 100% !important;
    display: block !important;
    margin: 100px 5px 10px 30px !important;
  }
  .single-input {
    position: relative !important;
    margin-bottom: 20px;
    width: 100%;
    
  }
   .label {
    position: absolute !important;
    top: 10px !important;
    left: 5px !important;
    pointer-events: none !important;
    transition: 0.3s !important;
    color: rgb(82, 82, 82);
    background:  transparent !important;
    font-size: 13px;
  }
  
   .input {
    background: rgb(14, 14, 14) !important;
    border: 1px solid rgb(14, 14, 14) !important;
    width: 100% !important;
    padding: 5px;
    height: 40px;
    outline: none !important;
    box-sizing: border-box;
    display: inline-block;
    transition: all .3s ease-in-out !important;
  }
  .input:focus ~ .label,
  .input:valid ~ .label
  {
    top: -15px !important;
    left: 0 !important;
    transform: scale(.8) !important;
    color: #ec2417 !important;
      
  }
  .input:focus,
  .input:valid
  {
      border:  1px solid #ec2417 !important;
  }
  .show-icon-cont{
      position: absolute !important;
      top: 8px;
      right: 13px;
      background: transparent !important;
  }
  .show-icon{
      color: #f68426;
      background: transparent !important;
      font-size: 20px !important;
  }
  .login-btn{
      margin: 30px 20px 10px 20px;
      font-size: 12px;
      font-weight: 600;
      letter-spacing: .07rem;
      background: linear-gradient(180deg, #f68426,#f68426,#f68426,#feee3e) !important;
      outline: none !important;
  }
  .login-pass{
      text-align: center;
      color: rgb(223, 223, 223) !important;
      font-size: 12px !important;
      letter-spacing: .04rem;
    margin-left: 30px;
    margin-bottom: 15px !important;
  }
  .login-opt{
      margin: 0px auto;
      padding-left: 20px;
      color: rgb(158, 158, 158) ;
      display: flex;
  }
  .line1{
      width: 120px;
      height: .08px;
      background: #461613;
      margin-top: 10px;
      margin-right: 10px;
  }
  .line2{
      width: 120px;
      height: .08px;
      background: #461613;
      margin-top: 10px;
      margin-left: 10px;
  }
  .login-social{
      display: flex !important;
      /* justify-content:space-between !important; */
      /* width: 100% ; */
      margin: 20px auto !important;
      padding-left: 70px;
  
  }
  .login-social-icon{
      font-size: 1.5rem !important;
      margin-right: 40px !important;
      transition: transform .5s ease-in-out;
  }
  .login-social-icon:hover{
      transform: scale(1.3);
  }
  .google{
      color: #ec2417;
  }
  .twitter{
      color: skyblue !important;
  }
  .facebook{
      color: rgb(46, 46, 250);
  }
  .login-acc{
      margin: 10px auto;
      color: rgb(158, 158, 158) ;
      font-size: 13px;
  }
  .login-link{
      color: #f68426;
      text-decoration: underline;
      font-weight: 600;
      padding-left: 10px;
  }
  .login-link:hover{
    color: #f68426 !important;
  }
  .single-inputs{
      padding-left: 25px;
  }
  .inputs{
      background: transparent !important;
      border: none !important;
  }
  .inputs:checked{
      color: #f68426 !important;
      background: #f68426 !important;
  }
  .labels{
      color: #f68426 !important;
  }
  
  @media (min-width:475px){
      .line1, .line2{
          width: 180px;
      }
      .login-opt{
          padding-left: 10px;
      }
  }
  